// angular
import { Routes } from '@angular/router';

// libs
import { MetaGuard } from '@ngx-meta/core';

// components
import { LoginComponent } from './components/login';
import { MainComponent } from './components/layout';
import { AdminComponent } from './components/admin/admin.component';
import { ChangeLanguageComponent } from './framework/i18n/i18n.module';

//import { AuthGuard } from './components/auth/_guards/index';

//admin routing
import { routes as adminRoutes } from './components/admin/admin.routes';

export const routes: Routes = [
    {
        path: 'admin-panel',
        component: LoginComponent,
        data: {
            meta: {
                title: 'PUBLIC.LOGIN.PAGE_TITLE'
            }
        }
    },
    {
        path: 'admin',
        component: AdminComponent,
        children: adminRoutes,
        data: {
            meta: {
                title: 'PUBLIC.LOGIN.PAGE_TITLE'
            }
        }
    },
    {
        path: '',
        component: MainComponent,
        children: [
            {
                path: '',
                loadChildren: './components/+home/home.module#HomeModule'
            },
            {
                path: 'about',
                loadChildren: './components/+about/about.module#AboutModule'
            },
            {
                path: 'secure-page',
                loadChildren: './components/+secure/secure.module#SecureModule'
            }
        ],
        data: {
            i18n: {
                isRoot: true
            }
        }
    },
    {
        path: 'change-language/:languageCode',
        component: ChangeLanguageComponent
    }/*,
    {
        path: '**',
        redirectTo: '',
        pathMatch: 'full'
    }*/
];


