import { Component, Inject } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { FormGroup, FormControl, FormBuilder, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';

import { FileValidators } from '../elements/file-validators';

import { RequestService } from './../services/request.service';

@Component({
    selector: 'modal-dialog',
    template: require('./dialog.component.html'),
    styles: [require('./dialog.component.scss')]
})
export class DialogComponent {
    requestGroup: FormGroup;

    constructor(public dialogRef: MatDialogRef<DialogComponent>, private _fb: FormBuilder, private service: RequestService) { }

    ngOnInit() {
        this.requestGroup = new FormGroup({
            firstName: new FormControl()
        });

        this.requestGroup = this._fb.group({
            email: [null, Validators.compose([Validators.required, Validators.email])],
            tel: [null, Validators.compose([Validators.required, Validators.minLength(9), Validators.maxLength(9)])],
            firstName: [null, Validators.required],
            lastName: null,
            message: null,
            multiplefile: [{ value: undefined, disabled: false }]   
        });
    }

    onSubmit() {
        console.log('SUBMITTED', this.requestGroup.value);
        this.service.sendRequest(this.requestGroup.value);
    }

    onNoClick(): void {
        console.log('call close method');
        this.dialogRef.close();
    }

    matcher = new MyErrorStateMatcher();
}

/** Error when invalid control is dirty, touched, or submitted. */
export class MyErrorStateMatcher implements ErrorStateMatcher {
    isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        const isSubmitted = form && form.submitted;
        return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
    }
}




