export class Request {
    firstName: string;
    lastName: string;
    email: string;
    tel: string;
    message: string;
    multiplefile: Array<string>;

    constructor(email: string, tel: string, firstName: string, lastName?: string, message?: string, multiplefile?: Array<string>) {
        this.email = email;
        this.tel = tel;
        this.firstName = firstName;
        this.lastName = lastName || '';
        this.message = message || '';
        this.multiplefile = multiplefile || [];
    }
}

export class RequestMeasure {
    name: string;
    tel: string;
    address: string;
    message: string;
    date: string;

    constructor(tel: string, name: string, date?: string, address?: string, message?: string) {
        this.tel = tel;
        this.name = name;
        this.address = address || '';
        this.message = message || '';
        this.date = date || '';
    }
}




