import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormControl, FormBuilder, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { Style, Product, EditProduct } from './../../../shared';
import { ActivatedRoute } from '@angular/router';

import { STYLES, PRODUCTS, EDITSTYLE } from './../../../mockdata/products';

import { AdminService } from './../../admin.service';

// data models
import { DataCategory } from './../../../shared';

@Component({
    selector: 'category',
    template: require('./category.component.html'),
    styles: [require('./category.component.scss')]
})
export class CategoryComponent implements OnInit, OnDestroy{
    addCategoryGroup: FormGroup;
    matcher = new MyErrorStateMatcher();
    private editCategory: DataCategory;
    private editMode: boolean = false;
    private sub: any;
    private id: number;
    public image: string;

    constructor(private route: ActivatedRoute, private _location: Location, private _fb: FormBuilder, private service: AdminService) { }

    ngOnInit() {

        this.addCategoryGroup = this._fb.group({
            common: this._fb.group({
                uk: this._fb.group({
                    name: [null, Validators.required],
                    description: null
                }),
                ru: this._fb.group({
                    name: [null, Validators.required],
                    description: null
                }),
                en: this._fb.group({
                    name: [null, Validators.required],
                    description: null
                }),
            }),
            data: this._fb.group({
                url: [null, Validators.required],
                image: null
            })
        });

        this.sub = this.route.params.subscribe(params => {

            //this.editCategory = EDITSTYLE;

            if (params.id){
                this.service.getCategory(params.id).subscribe(response => {
                    console.log('response edit cat: ', response);
                    this.editCategory = response;

                    if (this.editCategory && params.id) {
                        this.addCategoryGroup.patchValue(this.editCategory);
                        this.editMode = true;
                    }
                });

                this.id = params.id;
            }
            
        });
    }

    onSelectFile(event: any): void {
        let file = event.target.files[0];

        this.addCategoryGroup.patchValue({
            data: {
                image: file.name
            }
        });

        // We read the file and call the upload function with the result
        this.service.uploadFile(event.target.files[0]).subscribe(response => {
            this.image = response.name;
            
        });

    };

    onSubmit() {

        if (this.editMode) {
            
            this.addCategoryGroup.value['id'] = this.id;
            this.service.updateCategory(this.addCategoryGroup.value).subscribe(response => {
                console.log('Category was update', response);
                this.editMode = true;
                this._location.back();
            })
        } else {
            this.service.addCategory(this.addCategoryGroup.value).subscribe(response => {
                console.log('New Category was add', response);
                this.editMode = true;
                this._location.back();
            });
        }
    }

    back() {
        this._location.back();
    }

    getCategoryModeTitle() : string {
        return this.editMode ? 'Edit category' : 'Add category'
    }

    ngOnDestroy() {

        if (this.sub)
            this.sub.unsubscribe();
    }
}


/** Error when invalid control is dirty, touched, or submitted. */
export class MyErrorStateMatcher implements ErrorStateMatcher {
    isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        const isSubmitted = form && form.submitted;
        return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
    }
}


