// angular
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms'


// libs
// TODO: ngx-i18n-router
// import { I18NRouterModule } from '@ngx-i18n-router/core';

// framework
import { SharedModule } from '../../framework/core/shared.module';
import { MaterialModule } from '../../framework/material/material.module';

// routes & components
import { routes } from './admin.routes';
import { AdminComponent } from './admin.component';
import { SettingsComponent } from './settings';
import { ProductComponent } from './products';
import { AddProductComponent } from './products/add-product/add-product.component';
import { CategoriesComponent } from './categories';
import { CategoryComponent } from './categories/category/category.component';
import { HttpModule } from '@angular/http';

//import { FileValidators } from './../elements/file-validators';

// Services
import { AdminService } from './admin.service';

@NgModule({
  imports: [
    CommonModule,
    // TODO: ngx-i18n-router
    // I18NRouterModule.forChild(routes, 'home')
    RouterModule.forChild(routes),
    MaterialModule,
    SharedModule,
    ReactiveFormsModule,
    HttpModule
    ],
  providers: [AdminService],
  declarations: [AdminComponent, SettingsComponent, ProductComponent, AddProductComponent, CategoriesComponent, CategoryComponent],
})
export class AdminModule {
}



