import { Injectable } from '@angular/core';

import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';

import { Request, RequestMeasure } from './../shared/request';

@Injectable()
export class RequestService {

    constructor() { }

    sendRequest(data: any): Request{
        console.log('send request: ' + data);
        return new Request('s@mdvd', '+380979449612', 'Vova');
    }

    sendRequestMeasure(data: any): RequestMeasure {
        console.log('send request: ' + data);
        return new RequestMeasure('s@mdvd', '+380979449612', 'Vova');
    }
}


