import { Component } from '@angular/core';

import { PRODUCTS } from './../../mockdata/products';
import { DataSource } from '@angular/cdk/collections';

import { Observable } from 'rxjs/Observable';
import { Sort } from '@angular/material';

// data models
import { Product } from './../../shared';

@Component({
    selector: 'product',
    template: require('./products.component.html'),
    styles: [require('./products.component.scss')]
})
export class ProductComponent {
    sortedData: any;
    products: Product[];
    constructor() {
        this.sortedData = PRODUCTS.slice();
        this.products = PRODUCTS;
    }

    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.sortedData = PRODUCTS.filter((product) => {
            let regExp = new RegExp(filterValue.toString(), 'gi');
            return regExp.test(product.name.toString());
        });
    }

    sortData(sort: Sort) {
        const data = this.products.slice();
        if (!sort.active || sort.direction == '') {
            this.sortedData = data;
            return;
        }

        this.sortedData = data.sort((a, b) => {
            let isAsc = sort.direction == 'asc';
            switch (sort.active) {
                case 'id': return compare(+a.id, +b.id, isAsc);
                case 'name': return compare(a.name, b.name, isAsc);
                case 'style': return compare(a.style.name, b.style.name, isAsc);
                default: return 0;
            }
        });
    }  
}

function compare(a: any, b: any, isAsc: any) {
    return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}



