
// angular
import { Component, Inject, ViewChild } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';

// validators
import { FileValidators } from '../elements/file-validators';

// models
import { Settings } from './../../shared';

// services
import { AdminService } from './../admin.service';

@Component({
    selector: 'settings',
    template: require('./settings.component.html'),
    styles:[require('./settings.component.scss')]
})
export class SettingsComponent {
    settingGroup: FormGroup;
    logo: string = 'assets/images/placeholder.jpg';
    @ViewChild('preview-logo')
    previewImage: HTMLInputElement;
    private language: string = 'en';

    constructor(private _fb: FormBuilder, private service: AdminService) { }

    ngOnInit() {

        this.settingGroup = this._fb.group({
            email: [null, Validators.compose([Validators.required, Validators.email])],
            phone: [null, Validators.compose([Validators.required, Validators.minLength(9), Validators.maxLength(9)])],
            phone_2: null,
            address: null,
            about: null,
            logo: null,
            language: null
        });

        this.service.getSettings().subscribe(response => {
            console.log('response set', response);

            this.settingGroup.patchValue(response);

            this.logo = response.logo || 'assets/images/placeholder.png';
        })
    }

    onSelectFile(event: any): void {
        console.log('Select file : ' + event);
        console.log(event.target.files[0]);
        let file = event.target.files[0];
        let filePath = 'assets/uploads/' + file.name;
        this.settingGroup.patchValue({
            logo: filePath
        });

        // We read the file and call the upload function with the result
        this.service.uploadFile(event.target.files[0]).subscribe(response => {
            console.log('response---');
            console.log(response);
            this.logo = response.name;
        });
    };

    onSubmit() {
        let response;
        console.log(this.settingGroup.value);
        this.service.saveSettings(this.settingGroup.value as Settings).subscribe(res => {
            response = res;

            console.log('setting component response: ' + response);
        });
        
        
    }

    matcher = new MyErrorStateMatcher();
}

/** Error when invalid control is dirty, touched, or submitted. */
export class MyErrorStateMatcher implements ErrorStateMatcher {
    isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        const isSubmitted = form && form.submitted;
        return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
    }
}


