import { Component } from '@angular/core';

import { AdminService } from './../admin.service';
import { STYLES } from './../../mockdata/products';
import { DataSource } from '@angular/cdk/collections';

import { Observable } from 'rxjs/Observable';
import { Sort } from '@angular/material';

// data models
import { Style } from './../../shared';

@Component({
    selector: 'categories',
    template: require('./categories.component.html'),
    styles: [require('./categories.component.scss')]
})
export class CategoriesComponent {
    sortedData: any;
    categories: Style[];
    constructor(private service: AdminService) { }

    ngOnInit() {

        this.service.getCategories().subscribe(response => {
            this.sortedData = response.slice();
            this.categories = response;
        });
    }

    applyFilter(filterValue: string) {
        filterValue = filterValue.trim(); // Remove whitespace
        filterValue = filterValue.toLowerCase(); // MatTableDataSource defaults to lowercase matches
        this.sortedData = this.categories.filter((category) => {
            let regExp = new RegExp(filterValue.toString(), 'gi');
            return regExp.test(category.name.toString());
        });
    }

    sortData(sort: Sort) {
        const data = this.categories.slice();
        if (!sort.active || sort.direction == '') {
            this.sortedData = data;
            return;
        }

        this.sortedData = data.sort((a, b) => {
            let isAsc = sort.direction == 'asc';
            switch (sort.active) {
                case 'id': return compare(+a.id, +b.id, isAsc);
                case 'name': return compare(a.name, b.name, isAsc);
                default: return 0;
            }
        });
    }

    delete(id: number) {
        console.log('delete: ' + id);
        this.service.deleteCategory(id).subscribe(response => {
            console.log('response delete: ' + response);
            this.sortedData = response.slice();
            this.categories = response;
        });
    }  
}

function compare(a: any, b: any, isAsc: any) {
    return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}



