
// angular
import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs';
import 'rxjs/add/operator/map'
// data models
import { Settings, Product, Style, DataCategory } from './../shared';

@Injectable()
export class AdminService {

    private api = {
        uploadFile: '/api/uploadfile', settings: '/api/settings', product: '/api/product', products: '/api/products', category: '/api/category', categories: '/api/categories'
    }

    constructor(private http: Http) { }

    saveSettings(data: Settings): Observable<Settings> {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.put(this.api.settings, data, options).map((response: Response) => response.json());
    }

    getSettings(): Observable<Settings> {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.get(this.api.settings, options).map((response: Response) => response.json());
    }

    updateProduct(data: Product): Observable<Product> {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.put(this.api.product, data, options).map((response: Response) => response.json());
    }

    addProduct(data: Product): Observable<Product> {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.api.product, data, options).map((response: Response) => response.json());
    }

    getProduct(id: number): Observable<Product>{
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('productid', id.toString());

        let options = new RequestOptions({ headers: headers});

        return this.http.get(this.api.product, options).map((response: Response) => response.json());
    }

    getProducts(): Observable<Product[]> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.get(this.api.products, options).map((response: Response) => response.json());
    }

    updateCategory(data: DataCategory): Observable<Style> {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.put(this.api.category + '/' + data.id, data, options).map((response: Response) => response.json());
    }

    addCategory(data: DataCategory): Observable<DataCategory> {

        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.api.category, data, options).map((response: Response) => response.json());
    }

    deleteCategory(id: number): Observable<Style[]> {
        let headers = new Headers({ 'Content-Type': 'application/json' });

        return this.http.delete(this.api.category + '/' + id).map((response: Response) => response.json());
    }

    getCategory(id: number): Observable<DataCategory> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        headers.append('categoryid', id.toString());

        let options = new RequestOptions({ headers: headers });

        return this.http.get(this.api.category + '/' + id, options).map((response: Response) => response.json());
    }

    getCategories(): Observable<Style[]> {
        let headers = new Headers({ 'Content-Type': 'application/json' });
        let options = new RequestOptions({ headers: headers });

        return this.http.get(this.api.categories, options).map((response: Response) => response.json());
    }

    uploadFile(file: File): Observable<File> {
        let formData: FormData = new FormData();
        formData.append('uploadFile', file);
        let headers = new Headers();
        headers.append('Accept', 'application/json');
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.api.uploadFile, formData).map((response: Response) => response.json());
    }
}



