import { Component, Inject, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { FormGroup, FormControl, FormBuilder, FormArray, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { Style, Product, EditProduct } from './../../../shared';
import { ActivatedRoute } from '@angular/router';

import { STYLES, PRODUCTS, EDITPRODUCT } from './../../../mockdata/products';

@Component({
    selector: 'add-product',
    template: require('./add-product.component.html'),
    styles: [require('./add-product.component.scss')]
})
export class AddProductComponent implements OnInit, OnDestroy {
    addProductGroup: FormGroup;
    images: FormArray;
    stylesList: Style[];
    matcher = new MyErrorStateMatcher();
    selectedStyle: number;
    private editProduct: EditProduct;
    private editMode: boolean = false;
    private sub: any;

    constructor(private route: ActivatedRoute, private _location: Location, private _fb: FormBuilder) { }

    ngOnInit() {
        
        this.stylesList = STYLES;

        this.addProductGroup = this._fb.group({
            common: this._fb.group({
                ukrainian: this._fb.group({
                    name: [null, Validators.required],
                    description: null
                }),
                russian: this._fb.group({
                    name: [null, Validators.required],
                    description: null
                }),
                english: this._fb.group({
                    name: [null, Validators.required],
                    description: null
                }),
            }),
            data: this._fb.group({
                url: [null, Validators.required],
                image: [{ value: undefined, disabled: false }],
                style: null
            }),
            images: this._fb.array([this.createImage()]),
        });

        this.sub = this.route.params.subscribe(params => {

            this.editProduct = EDITPRODUCT;

            if (this.editProduct && params.id) {
                this.addProductGroup.patchValue(this.editProduct);
                this.selectedStyle = +this.editProduct.data.style.id;
                this.editMode = true;
            }            
        });
    }

    onSubmit() {

        if (this.editMode) {
            console.log('Edit', this.addProductGroup.value);
        } else {
            console.log('Add', this.addProductGroup.value);
        }
        
    }

    createImage(): FormGroup {
        return this._fb.group({
            image: [{ value: undefined, disabled: false }]
        });
    }

    addImage(): void {
        this.images = this.addProductGroup.get('images') as FormArray;
        this.images.push(this.createImage());
    }

    deleteImage(index: number): void {
        this.images.removeAt(index);
    }

    back() {
        this._location.back();
    }

    //edit code

    ngOnDestroy() {
        this.sub.unsubscribe();
    }
} 


/** Error when invalid control is dirty, touched, or submitted. */
export class MyErrorStateMatcher implements ErrorStateMatcher {
    isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean {
        const isSubmitted = form && form.submitted;
        return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
    }
}


